import { BrowserRouter, Route, Routes } from "react-router-dom";
import { useEffect } from "react";
import axios from "axios";
import Main from "./components/main/Main";
import Header from "./components/header/Header";
import MyStore from "./components/myStore/MyStore";
import ProductManagement from "./components/myStore/ProductManagement";
import Cookie from "./components/Main";
import { API } from "./config";

const layoutStyle = {
  minWidth: "1200px",
};
axios.defaults.withCredentials = true;
function App() {
  useEffect(() => {
    axios
      .get(
        "https://usedauction.shop/api/products?categoryId=1&productName=mythenmatz&orderBy=VIEW_ORDER&page=1&size=10",
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      )
      .then((response) => {
        console.log("결과", response);
      });
  }, []);
  return (
    <div style={layoutStyle}>
      <BrowserRouter>
        <Header></Header>
        <Routes>
          <Route path="/" element={<Main></Main>}></Route>
          <Route path="/myStore" element={<MyStore />}></Route>
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
